body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1rem;
  background-color: aliceblue;
}

#bod {
  background-color: aliceblue;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-link {
  font-size: 1.5rem;
}

#proj {
  width: fit-content;
  position: relative;
}

.g-card {
  margin: 20px;
  width: 1082.550px;
  border-radius: 33px;
  background: #f0f8ff;
  box-shadow:  20px -20px 15px #ccd3d9, 
               -40px 40px 32px #ffffff;
               padding-bottom: 15px;
               padding-top: 25px;
               padding-left: 25px;
               padding-right: 25px;
}




.g-card-image {
  max-width: 100%;
  border-radius: 10px;
  width: auto;

  box-shadow: 0px 0px 3px 1px #ccc;
}


.g-card-info {
  margin-top: 10px;
  min-height: 100px;
}


.g-card-title {
  font-size: 24px;
  margin: 0px;
}


.g-card-sub-title {
  font-size: 16px;
  margin: 0px;
}


.success-msg {
  color: #57A773;
  padding: 10px 15px;
}


.err-msg {
  color: #EE6352;
  padding: 10px 15px;
}