.link {
  color: white;
  text-decoration: none ;
}
.link:hover {
  color: white;
  background-color: white;
  text-decoration: none ;
}

#linkBox {
  list-style:none; border:1px solid black;
  width: fit-content;
  z-index: 1;
  position: relative;
  color: white;
  padding: 0.5em 1em;
  outline: none;
  border: none;
  background-color: #89aecf;
}

#linkBox::before {
  content: '';
  z-index: -1;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: black;
  transform-origin: center right;
  transform: scaleX(0);
  transition: transform 0.25s ease-in-out;
}

#linkBox:hover {
  cursor: pointer;
}

#linkBox:hover::before {
  transform-origin: center left;
  transform: scaleX(1);
}
